import React from "react";
import styled from "styled-components";
// Components
import ProjectBoxB from "../Elements/ProjectBoxB";
import FullButton from "../Buttons/FullButton";
// Assets
import ProjectImg1 from "../../assets/img/noticias/caracoto.png";
import ProjectImg2 from "../../assets/img/noticias/ayaviri.png";
import ProjectImg3 from "../../assets/img/noticias/carreras.png";
import ProjectImg4 from "../../assets/img/projects/4.png";
import ProjectImg5 from "../../assets/img/projects/5.png";
import ProjectImg6 from "../../assets/img/projects/6.png";
import AddImage2 from "../../assets/img/add/add2.png";

export default function Noticias() {
  return (
    <Wrapper id="noticias">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Noticias</h1>
            {/* <p className="font13">
              Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut
              <br />
              labore et dolore magna aliquyam erat, sed diam voluptua.
            </p> */}
          </HeaderInfo>
          <div className="row textCenter">
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBoxB
                img={ProjectImg1}
                title="EN CARACOTO SERÁ LA NUEVA SEDE DE LA UNA PUNO"
                text="Próximamente será una realidad la sede de la Universidad Nacional del Altiplano de Puno y será en el distrito de Caracoto, jurisdicción de la provincia de San Román. Así, lo anunció, el Rector de la UNA Puno, Dr. Paulino Machaca Ari, tras una importante reunión que mantuvo con el alcalde de dicho distrito, Javier Phocco Cuno, y en los siguientes días se concretará la firma de convenio para la donación de más de 30 hectáreas."
                action={() => window.open("https://portal.unap.edu.pe/noticia/en-caracoto-sera-la-nueva-filial-de-la-una-puno?utm_source=chatgpt.com", "_blank")}
                
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBoxB
                img={ProjectImg2}
                title="CONSEJO UNIVERSITARIO APRUEBA CREACIÓN DE LA SEDE EN AYAVIRI"
                text="En una decisión histórica, el Consejo Universitario de la Universidad Nacional del Altiplano de Puno, aprobó la creación de una nueva sede en la ciudad de Ayaviri, capital de la provincia de Melgar. El Rector, Dr. Paulino Machaca Ari, sustentó la necesidad y el alcalde provincial, regidores y población en su conjunto, agradecieron por acercar la educación superior a dicha provincia."
               action={() => window.open("https://portal.unap.edu.pe/noticia/consejo-universitario-aprueba-creacion-de-filial-en-ayaviri", "_blank")}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBoxB
                img={ProjectImg3}
                title="UNA PUNO ABRIRÁ CINCO NUEVAS CARRERAS ANTES DE 2025"
                text="La Universidad Nacional del Altiplano (UNA) de Puno implementará cinco nuevas escuelas profesionales antes del inicio del año académico 2025, informó el vicerrector académico, Mario Cuentas Alvarado. Entre las carreras destacan Ingeniería de Inteligencia Artificial, Ingeniería de Datos e Ingeniería de Seguridad Industrial, las cuales buscan responder a las demandas del mercado laboral actual."

                action={() => window.open("https://asup.edu.pe/2025/02/06/una-puno-abrira-cinco-nuevas-carreras-antes-de-2025/?utm_source=chatgpt.com", "_blank")}
              />
            </div>
          </div>
        </div>
        <br />
        <br />
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Advertising = styled.div`
  padding: 100px 0;
  margin: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 60px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  position: relative;
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
  }
`;
const AddLeftInner = styled.div`
  width: 100%;
  position: absolute;
  top: -300px;
  left: 0;
  @media (max-width: 1190px) {
    top: -250px;
  }
  @media (max-width: 920px) {
    top: -200px;
  }
  @media (max-width: 860px) {
    order: 1;
    position: relative;
    top: -60px;
    left: 0;
  }
`;
const ImgWrapper = styled.div`
  width: 100%;
  padding: 0 15%;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 400px) {
    padding: 0;
  }
`;
