import React, { useState } from "react";
import styled from "styled-components";
import { Link } from "react-scroll";
// Assets
import CloseIcon from "../../assets/svg/CloseIcon";
import LogoUnap from "../../assets/img/logo_unap.png";

export default function Sidebar({ sidebarOpen, toggleSidebar }) {
  // Estados para los menús desplegables
  const [sedesOpen, setSedesOpen] = useState(false);
  const [biblioOpen, setBiblioOpen] = useState(false);

  return (
    <Wrapper className="animate darkBg" sidebarOpen={sidebarOpen}>
      <SidebarHeader className="flexSpaceCenter">
        <Link to="home" spy={true} smooth={true} offset={-80} onClick={() => toggleSidebar(false)}>
          <div className="flexNullCenter">
            <img src={LogoUnap} alt="Logo UNAP" style={{ height: "40px" }} />
          </div>
        </Link>
        <CloseBtn onClick={() => toggleSidebar(false)} className="animate pointer">
          <CloseIcon />
        </CloseBtn>
      </SidebarHeader>

      <UlStyle className="flexNullCenter flexColumn">
        <li className="semiBold font15 pointer">
          <Link
            onClick={() => toggleSidebar(false)}
            activeClass="active"
            className="whiteColor"
            style={{ padding: "10px 15px" }}
            to="home"
            spy={true}
            smooth={true}
            offset={-80}
          >
            Inicio
          </Link>
        </li>

        <li className="semiBold font15 pointer">
          <Link
            onClick={() => toggleSidebar(false)}
            activeClass="active"
            className="whiteColor"
            style={{ padding: "10px 15px" }}
            to="services"
            spy={true}
            smooth={true}
            offset={-80}
          >
            Servicios
          </Link>
        </li>

        {/* Menú desplegable de Sedes - Corregido */}
        <li className="semiBold font15 pointer flexColumn">
          <DropdownButton onClick={() => setSedesOpen(!sedesOpen)}>
            Sedes
            <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg" 
              style={{ marginLeft: '10px', transform: sedesOpen ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.3s ease' }}>
              <path d="M5 2.5L10 7.5L8.5 9L5 5.5L1.5 9L0 7.5L5 2.5Z" fill="white"/>
            </svg>
          </DropdownButton>
          {sedesOpen && (
            <SubMenu>
              <li>
                <Link 
                  to="azangaro" 
                  spy={true} 
                  smooth={true} 
                  offset={-80}
                  onClick={() => toggleSidebar(false)}
                >
                  Sede Azángaro
                </Link>
              </li>
              <li>
                <Link 
                  to="juli" 
                  spy={true} 
                  smooth={true} 
                  offset={-80}
                  onClick={() => toggleSidebar(false)}
                >
                  Sede Juli
                </Link>
              </li>
            </SubMenu>
          )}
        </li>

        <li className="semiBold font15 pointer">
          <Link
            onClick={() => toggleSidebar(false)}
            activeClass="active"
            className="whiteColor"
            style={{ padding: "10px 15px" }}
            to="malla"
            spy={true}
            smooth={true}
            offset={-80}
          >
            Mallas Curriculares
          </Link>
        </li>

        {/* Nuevo menú desplegable de Biblioteca Virtual */}
        <li className="semiBold font15 pointer flexColumn">
          <DropdownButton onClick={() => setBiblioOpen(!biblioOpen)}>
            Biblioteca Virtual
            <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg" 
              style={{ marginLeft: '10px', transform: biblioOpen ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.3s ease' }}>
              <path d="M5 2.5L10 7.5L8.5 9L5 5.5L1.5 9L0 7.5L5 2.5Z" fill="white"/>
            </svg>
          </DropdownButton>
          {biblioOpen && (
            <SubMenu>
              <li>
                <a 
                  href="https://biblioteca.unap.edu.pe/bibliovirtual/" 
                  target="_blank"
                  rel="noopener noreferrer"
                  onClick={() => toggleSidebar(false)}
                >
                  Ingreso (Login)
                </a>
              </li>
              <li>
                <a 
                  href="https://biblioteca.unap.edu.pe/opac_css/index.php" 
                  target="_blank"
                  rel="noopener noreferrer"
                  onClick={() => toggleSidebar(false)}
                >
                  Sistema de Bibliotecas
                </a>
              </li>
              <li>
                <a 
                  href="https://drive.google.com/file/d/1-0VZO7YvW1ROny9CL4KO90qqMjv8FyCb/view" 
                  target="_blank"
                  rel="noopener noreferrer"
                  onClick={() => toggleSidebar(false)}
                >
                  Manual de Usuario
                </a>
              </li>
            </SubMenu>
          )}
        </li>
        
        <li className="semiBold font15 pointer">
          <Link
            onClick={() => toggleSidebar(false)}
            className="whiteColor radius8 lightBg"
            style={{ padding: "10px 15px" }}
            to="noticias"
            spy={true}
            smooth={true}
            offset={-80}
          >
            Noticias
          </Link>
        </li>
      </UlStyle>
    </Wrapper>
  );
}

// --- STYLED COMPONENTS ---
const Wrapper = styled.nav`
  width: 400px;
  height: 100vh;
  position: fixed;
  top: 0;
  padding: 0 30px;
  right: ${(props) => (props.sidebarOpen ? "0px" : "-400px")};
  z-index: 9999;
  @media (max-width: 400px) {
    width: 100%;
  }
`;
const SidebarHeader = styled.div`
  padding: 20px 0;
`;
const CloseBtn = styled.button`
  border: 0px;
  outline: none;
  background-color: transparent;
  padding: 10px;
`;
const UlStyle = styled.ul`
  padding: 40px;
  li {
    margin: 20px 0;
  }
`;

const DropdownButton = styled.div`
  cursor: pointer;
  width: 100%;
  text-align: center;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
`;

const SubMenu = styled.ul`
  list-style: none;
  padding: 10px 0 0 20px;
  width: 100%;
  
  li {
    margin: 15px 0;
  }
  
  a, a:visited {
    color: #c9c9c9;
    text-decoration: none;
    font-weight: 500;
    display: block;
    
    &:hover {
      color: white;
    }
  }
`;