import React from "react";
import styled from "styled-components";
// Components
import FullButton from "../Buttons/FullButton";
// Assets
import HeaderImage from "../../assets/img/SEDE/azangaro2.png";

import QuotesIcon from "../../assets/svg/Quotes";
import Dots from "../../assets/svg/Dots";

export default function Header() {
  return (
    <Wrapper id="azangaro" className="container flexSpaceCenter">
      <LeftSide className="flexCenter">
        <div>
          <h1 className="extraBold font60">Sede Azángaro</h1>
          <HeaderP className="font13 semiBold">
          La Universidad Nacional del Altiplano (UNA) – Sede Azángaro con Secciones de las Carreras Profesionales de Ingeniería de Minas, Ingeniería Económica e Ingeniería de Telecomunicaciones        
          aprobada para la zona norte de Puno. Su inauguración oficial se realizó en agosto de 2024, en un acto con la presencia del rector Dr. Paulino Machaca y autoridades locales. Con esta sede se descentraliza la educación superior para la población quechua de la provincia de Azángaro.
          <br />
          <CareersList>
            <CareerItem className="mining">
              <a href="https://minas.unap.edu.pe/" target="_blank" > Ingeniería de Minas</a>
             </CareerItem>
            <CareerItem className="economics"><a href="https://www.fieunap.edu.pe/" target="_blank" >Ingeniería Económica</a></CareerItem>
            <CareerItem className="telecom"><a href="https://portal.unap.edu.pe/" target="_blank" >Ingeniería de Telecomunicaciones</a></CareerItem>
          </CareersList>
          </HeaderP>
          <StatsWrapper>
            <StatItem>
              <StatNumber className="extraBold">3</StatNumber>
              <StatLabel>Carreras profesionales</StatLabel>
            </StatItem>
            <StatItem>
              <StatNumber className="extraBold">2024</StatNumber>
              <StatLabel>Año de inauguración</StatLabel>
            </StatItem>
          </StatsWrapper>
        </div>
      </LeftSide>
      <RightSide>
        <ImageWrapper>
          <ImageWrapper2>
            <Img2 src={HeaderImage} alt="office" />
          </ImageWrapper2>
          
          <DotsWrapper>
            <Dots />
          </DotsWrapper>
        </ImageWrapper>
        <GreyDiv className="lightBg"></GreyDiv>
      </RightSide>
    </Wrapper>
  );
}
// Estilos para las carreras resaltadas
const CareersList = styled.div`
  display: inline-flex;
  flex-direction: column;
  gap: 5px;
  margin: 8px 0;
`;

const CareerItem = styled.span`
  font-color: #000;
  display: inline-block;
  font-weight: 600;
  padding: 3px 10px;
  margin: 2px 0;
  border-radius: 4px;
  transition: all 0.3s ease;
  
  &.mining {
    background: rgba(158, 158, 158, 0.2);
    border-left: 3px solid #606060ff;
    color: #000;
  }
  
  &.economics {
    background: rgba(158, 158, 158, 0.2);
    border-left: 3px solid #606060ff;
    color: #000;
  }
  
  &.telecom {
    background: rgba(158, 158, 158, 0.2);
    border-left: 3px solid #606060ff;
    color: #000;
  }
  
  &:hover {
    transform: translateX(3px);
  }
`;
// Nuevos componentes para mostrar estadísticas
const StatsWrapper = styled.div`
  display: flex;
  gap: 40px;
  margin-bottom: 30px;
  
  @media (max-width: 960px) {
    justify-content: center;
    gap: 30px;
  }
  
  @media (max-width: 560px) {
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }
`;

const StatItem = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  
  @media (max-width: 960px) {
    align-items: center;
  }
`;

const StatNumber = styled.div`
  font-size: 2.5rem;
  color: #7620ff;
  line-height: 1;
`;

const StatLabel = styled.div`
  font-size: 0.9rem;
  color: #666;
  margin-top: 5px;
`;

// Componentes originales con pequeños ajustes
const Img2 = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.8s ease;
  
  &:hover {
    transform: scale(1.05);
  }
`;

const ImageWrapper2 = styled.div`
  display: flex;
  justify-content: flex-end;
  position: relative;
  z-index: 9;
  width: 100%;
  max-width: 420px;
  aspect-ratio: 3 / 4;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.15);
  
  @media (max-width: 960px) {
    width: 100%;
    justify-content: center;
    max-width: 350px;
    margin: 0 auto;
  }
`;

const Wrapper = styled.section`
  padding-top: 80px;
  width: 100%;
  min-height: 840px;
  @media (max-width: 960px) {
    flex-direction: column;
  }
`;

const LeftSide = styled.div`
  width: 50%;
  height: 100%;
  @media (max-width: 960px) {
    width: 100%;
    order: 2;
    margin: 50px 0;
    text-align: center;
  }
  @media (max-width: 560px) {
    margin: 80px 0 50px 0;
  }
`;

const RightSide = styled.div`
  width: 50%;
  height: 100%;
  @media (max-width: 960px) {
    width: 100%;
    order: 1;
    margin-top: 30px;
  }
`;

const HeaderP = styled.div`
  max-width: 470px;
  padding: 15px 0 30px 0;
  line-height: 1.5rem;
  @media (max-width: 960px) {
    padding: 15px 0 30px 0;
    text-align: center;
    max-width: 100%;
  }
`;

const BtnWrapper = styled.div`
  max-width: 250px;
  @media (max-width: 960px) {
    margin: 0 auto;
  }
`;

const GreyDiv = styled.div`
  width: 30%;
  height: 700px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  @media (max-width: 960px) {
    display: none;
  }
`;

const ImageWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  position: relative;
  z-index: 9;
  @media (max-width: 960px) {
    width: 100%;
    justify-content: center;
  }
`;

const QuoteWrapper = styled.div`
  position: absolute;
  left: 0;
  bottom: 50px;
  max-width: 330px;
  padding: 30px;
  z-index: 99;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  backdrop-filter: blur(5px);
  
  @media (max-width: 960px) {
    left: 20px;
  }
  @media (max-width: 560px) {
    bottom: -50px;
  }
`;

const QuotesWrapper = styled.div`
  position: absolute;
  left: -20px;
  top: -10px;
`;

const DotsWrapper = styled.div`
  position: absolute;
  right: -100px;
  bottom: 100px;
  z-index: 2;
  @media (max-width: 960px) {
    right: 100px;
  }
  @media (max-width: 560px) {
    display: none;
  }
`;

