import React from "react";
import styled from "styled-components";
// Components
import ProjectBox from "../Elements/ProjectBox";
import FullButton from "../Buttons/FullButton";
// Assets
import ProjectImg1 from "../../assets/img/escuelas/arqui.png";
import ProjectImg2 from "../../assets/img/escuelas/agroindus.png";
import ProjectImg3 from "../../assets/img/escuelas/contables.png";
import ProjectImg4 from "../../assets/img/projects/4.png";
import ProjectImg5 from "../../assets/img/projects/5.png";
import ProjectImg6 from "../../assets/img/projects/6.png";
import AddImage2 from "../../assets/img/add/add2.png";


//pdf
import MallaAgroPDF from "../../assets/pdfs/AGRO_MC.pdf";
import MallaArquiPDF from "../../assets/pdfs/ARQUI_MC.pdf";
import MallaContaPDF from "../../assets/pdfs/CONTA_MC.pdf";
import MallaEconoPDF from "../../assets/pdfs/ECONO_MC.pdf";
import MallaMinasPDF from "../../assets/pdfs/MINAS_MC.pdf";
import MallaTelePDF from "../../assets/pdfs/TELECO_MC.pdf";

export default function Escuelas() {
  return (
    <Wrapper id="escuelas">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Escuelas Profesionales</h1>
            <p className="font13">
              La sede Juli ofrece los siguientes programas académicos
            </p>
          </HeaderInfo>
          <div className="row textCenter">
          <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
            <ProjectBox
              img={ProjectImg1}
              title="Escuela Profesional de Arquitectura y Urbanismo"
              text="Forma profesionales capaces de diseñar, planificar y gestionar espacios urbanos y arquitectónicos, integrando criterios estéticos, funcionales, sociales y ambientales."
              action={() => window.open("https://arquitectura.ficaunap.edu.pe/", "_blank")}
              nombrebotton={"Plan de Estudios"}
              link={MallaArquiPDF}
            />
          </div>

          <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
            <ProjectBox
              img={ProjectImg2}
              title="Escuela Profesional de Ingeniería Agroindustrial"
              text="Prepara especialistas en el desarrollo, transformación y gestión de procesos productivos agroindustriales, garantizando la calidad, innovación y sostenibilidad de los recursos."
              action={() => window.open("https://epiai-unap.net/", "_blank")}
              nombrebotton={"Plan de Estudios"}
              link={MallaAgroPDF}
            />
          </div>

          <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
            <ProjectBox
              img={ProjectImg3}
              title="Escuela Profesional de Ciencias Contables"
              text="Orienta la formación de profesionales en contabilidad, auditoría y finanzas, con capacidad para gestionar información financiera y apoyar la toma de decisiones estratégicas."
              action={() => window.open("https://portal.unap.edu.pe/", "_blank")}
              nombrebotton={"Plan de Estudios"}
              link={MallaContaPDF}
            />
          </div>
          </div>
          
          {/* <div className="row flexCenter">
            <div style={{ margin: "50px 0", width: "200px" }}>
              <FullButton title="Load More" action={() => alert("clicked")} />
            </div>
          </div> */}
          <br />
          <br />
        </div>
      </div>
      {/* <div className="lightBg">
        <div className="container">
          <Advertising className="flexSpaceCenter">
            <AddLeft>
              <AddLeftInner>
                <ImgWrapper className="flexCenter">
                  <img className="radius8" src={AddImage2} alt="add" />
                </ImgWrapper>
              </AddLeftInner>
            </AddLeft>
            <AddRight>
              <h4 className="font15 semiBold">A few words about company</h4>
              <h2 className="font40 extraBold">A Study of Creativity</h2>
              <p className="font12">
                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed
                diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum.
              </p>
              <ButtonsRow className="flexNullCenter" style={{ margin: "30px 0" }}>
                <div style={{ width: "190px" }}>
                  <FullButton title="Get Started" action={() => alert("clicked")} />
                </div>
                <div style={{ width: "190px", marginLeft: "15px" }}>
                  <FullButton title="Contact Us" action={() => alert("clicked")} border />
                </div>
              </ButtonsRow>
            </AddRight>
          </Advertising>
        </div>
      </div> */}
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Advertising = styled.div`
  padding: 100px 0;
  margin: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 60px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  position: relative;
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
  }
`;
const AddLeftInner = styled.div`
  width: 100%;
  position: absolute;
  top: -300px;
  left: 0;
  @media (max-width: 1190px) {
    top: -250px;
  }
  @media (max-width: 920px) {
    top: -200px;
  }
  @media (max-width: 860px) {
    order: 1;
    position: relative;
    top: -60px;
    left: 0;
  }
`;
const ImgWrapper = styled.div`
  width: 100%;
  padding: 0 15%;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 400px) {
    padding: 0;
  }
`;
