import React from "react";
import styled from "styled-components";
import Slider from "react-slick";
import { FaChevronRight, FaChevronLeft } from "react-icons/fa"; // Importamos los iconos

// Estilos de react-slick
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

// Componentes
import ClientSlider from "../Elements/ClientSlider";
import ServiceBox from "../Elements/ServiceBox";
import FullButton from "../Buttons/FullButton";

// --- PASO 1: Importa solo los archivos PDF ---
import MallaAgroPDF from "../../assets/pdfs/AGRO_MC.pdf";
import MallaArquiPDF from "../../assets/pdfs/ARQUI_MC.pdf";
import MallaContaPDF from "../../assets/pdfs/CONTA_MC.pdf";
import MallaEconoPDF from "../../assets/pdfs/ECONO_MC.pdf";
import MallaMinasPDF from "../../assets/pdfs/MINAS_MC.pdf";
import MallaTelePDF from "../../assets/pdfs/TELECO_MC.pdf";

// --- PASO 2: Simplifica la estructura de datos ---
// Ya no necesitas una 'imageSrc', solo el PDF.
const curriculaData = [
  {
    id: 1,
    pdfSrc: MallaAgroPDF,
    title: "Ingeniería Agroindustrial",
    campus: "Sede Juli",
  },
  {
    id: 2,
    pdfSrc: MallaArquiPDF,
    title: "Arquitectura y Urbanismo",
    campus: "Sede Juli",
  },
  {
    id: 3,
    pdfSrc: MallaContaPDF,
    title: "Contabilidad",
    campus: "Sede Juli",
  },
  {
    id: 3,
    pdfSrc: MallaEconoPDF,
    title: "Ingeniería Económica",
    campus: "Sede Azángaro",
  },
  {
    id: 3,
    pdfSrc: MallaMinasPDF,
    title: "Ingeniería de Minas",
    campus: "Sede Azángaro",
  },
  {
    id: 3,
    pdfSrc: MallaTelePDF,
    title: "Ingeniería de Telecomunicaciones",
    campus: "Sede Azángaro",
  },
];

// --- Componentes de Flecha Personalizados MODIFICADOS ---
const CurriculumArrow = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
  cursor: pointer;
  width: 70px;
  height: 70px;
  background-color: rgba(118, 32, 255, 0.8); /* Color morado más visible */
  border-radius: 50%;
  transition: all 0.3s ease;
  color: white;
  font-size: 28px; /* Tamaño de icono más grande */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);

  &:hover {
    background-color: rgba(118, 32, 255, 1);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
    transform: translateY(-50%) scale(1.05);
  }

  /* MODIFICADO: Posicionamiento fuera del PDF */
  &.next { right: -35px; } /* Ahora está más afuera */
  &.prev { left: -35px; } /* Ahora está más afuera */
  
  @media (max-width: 1024px) {
    width: 60px;
    height: 60px;
    font-size: 24px;
    &.next { right: -30px; }
    &.prev { left: -30px; }
  }
  
  @media (max-width: 768px) {
    width: 50px;
    height: 50px;
    font-size: 20px;
    &.next { right: -25px; }
    &.prev { left: -25px; }
  }
`;

const CustomNextArrow = ({ onClick }) => (
  <CurriculumArrow className="next" onClick={onClick}>
    <FaChevronRight />
  </CurriculumArrow>
);

const CustomPrevArrow = ({ onClick }) => (
  <CurriculumArrow className="prev" onClick={onClick}>
    <FaChevronLeft />
  </CurriculumArrow>
);

// --- PASO 3: Modifica el componente del carrusel para usar <embed> ---
const CurriculumCarousel = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    nextArrow: <CustomNextArrow />, // Flecha personalizada
    prevArrow: <CustomPrevArrow />, // Flecha personalizada
    autoplay: false,
  };

  return (
    <CarouselContainer>
      <Slider {...settings}>
        {curriculaData.map((curriculum) => (
          <SlideItem key={curriculum.id}>
            {/* Aquí está la magia: incrustamos el PDF directamente */}
            <PdfEmbed 
              src={curriculum.pdfSrc} 
              type="application/pdf"
            />
            <SlideCaption>
              <strong>{curriculum.title}</strong><br />
              <span>{curriculum.campus}</span>
            </SlideCaption>
          </SlideItem>
        ))}
      </Slider>
    </CarouselContainer>
  );
};

// --- Componente Principal (sin cambios en la estructura) ---
export default function Services() {
  return (
    <Wrapper id="services">
      <div className="lightBg" style={{ padding: "50px 0" }}>
        <div className="container">
          <ClientSlider />
        </div>
      </div>
      <div className="whiteBg" style={{ padding: "60px 0" }}>
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Servicios</h1>
            <p className="font13">
              En la sedes los estudiantes podrán acceder a diversos servicios universitarios. También, se habilitarán procesos de matrícula y secretaría académica locales, así como laboratorios y talleres de apoyo para las carreras.
            </p>
          </HeaderInfo>
          <ServiceBoxRow className="flex">
            <ServiceBoxWrapper>
              <ServiceBox
                icon="discusion"
                title="Tutoría Académica"
                subtitle="La universidad brinda un sistema de tutoría que acompaña al estudiante desde su ingreso hasta la culminación de la carrera. En este espacio, los alumnos reciben orientación personalizada para mejorar su rendimiento académico, reforzar hábitos de estudio y resolver dificultades en cursos específicos."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="aprendiendo"
                title="Matrícula y secretaría académica locales"
                subtitle="Con el objetivo de descentralizar procesos, la sede dispone de oficinas administrativas donde los estudiantes pueden realizar trámites de matrícula, constancias, certificados y gestiones académicas sin necesidad de trasladarse a la ciudad de Puno."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox
                icon="instructor"
                title="Bienestar Estudiantil"
                subtitle="La institución brinda un enfoque de apoyo integral que incluye servicios relacionados con la salud, psicología, tutoría, transporte, deporte y alimentación."
              />
            </ServiceBoxWrapper>
            <ServiceBoxWrapper>
              <ServiceBox icon="printer" title="Biblioteca digital y Recursos de estudio" 
              subtitle="Los estudiantes tienen acceso a la biblioteca digital institucional, que permite consultar libros, revistas académicas, tesis y otros materiales bibliográficos en línea. Este recurso fortalece la investigación y facilita el aprendizaje autónomo." />
            </ServiceBoxWrapper>
          </ServiceBoxRow>
        </div>
        <div className="lightBg" id="malla">
          <div className="container">
            <Advertising className="flexSpaceCenter">
              <AddLeft>
                <h4 className="font15 semiBold">Explora nuestras carreras</h4>
                <h2 className="font40 extraBold">Mallas Curriculares</h2>
                <p className="font12">
                  Usa las flechas para navegar entre los planes de estudio de nuestras carreras disponibles. Puedes interactuar directamente con cada documento.
                </p>
               
              </AddLeft>
              <AddRight>
                <CurriculumCarousel />
              </AddRight>
            </Advertising>
          </div>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section` width: 100%; `;
const ServiceBoxRow = styled.div` @media (max-width: 860px) { flex-direction: column; } `;
const ServiceBoxWrapper = styled.div`
  width: 20%; margin-right: 5%; padding: 80px 0;
  @media (max-width: 860px) { width: 100%; text-align: center; padding: 40px 0; }
`;
const HeaderInfo = styled.div` @media (max-width: 860px) { text-align: center; } `;



// --- STYLED COMPONENTS AJUSTADOS PARA ANCHO COMPLETO ---

const Advertising = styled.div`
  /* MODIFICADO: Reducido el padding superior */
  width: 100%;
  padding: 50px 0 100px 0; 
  
  /* El resto de tus estilos para Advertising se mantienen */
  margin: 0;
  position: relative;

  @media (max-width: 1160px) {
    padding: 30px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 20px 0 30px 0;
  }
`;

const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;

const AddLeft = styled.div`
  width: 80%;
  /* AÑADIDO: Padding interno para que el texto no se pegue al borde */
  padding: 0 5%; 
  box-sizing: border-box; /* Importante para que el padding no afecte el ancho */

  p {
    max-width: 100%; /* El texto ahora puede ser más ancho */
  }
  @media (max-width: 860px) {
    width: 100%; /* En móvil ocupa todo el ancho */
    padding: 0 10%; /* Mantenemos un buen padding en móvil */
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;

const AddRight = styled.div`
  width: 80%;
  /* MODIFICADO: Aumentado padding para dar espacio a las flechas */
  padding: 0 3%;
  box-sizing: border-box;
  position: relative; /* Para el posicionamiento absoluto de las flechas */

  @media (max-width: 860px) {
    width: 100%;
    padding: 0 5%; /* Reducido para dispositivos móviles */
    order: 1;
    margin-bottom: 30px;
  }
`;

// Ajustar el contenedor del carrusel para dar espacio a las flechas
const CarouselContainer = styled.div`
  width: 100%; /* MODIFICADO: Reducido del 100% al 90% para dar espacio a las flechas */
  margin: 0 auto; /* AÑADIDO: Centrar el contenedor */
  position: relative;
  
  /* Mejoramos la visibilidad de los puntos de navegación */
  .slick-dots {
    bottom: -15px;
    
    li button:before {
      font-size: 12px;
      color: rgba(118, 32, 255, 0.5);
      opacity: 0.7;
    }
    
    li.slick-active button:before {
      color: rgba(118, 32, 255, 0.9);
      opacity: 1;
    }
  }
  
  /* Elimina cualquier padding que pudiera afectar la posición */
  .slick-slider {
    padding: 0;
  }
`;

const SlideItem = styled.div`
  outline: none;
  border: none;
`;

const PdfEmbed = styled.embed`
  width: 100%;
  height: 850px; /* MODIFICADO: Aumentado de 700px a 850px */
  border-radius: 8px;
  border: 1px solid #ddd;
  
  @media (max-height: 900px) {
    height: 700px; /* Altura adaptativa para pantallas medianas (aumentada) */
  }
  
  @media (max-height: 700px) {
    height: 550px; /* Aumentado para pantallas pequeñas */
  }
`;

const SlideCaption = styled.div`
  text-align: center;
  padding: 20px 0;
  font-size: 16px;
  color: #333;
  strong {
    font-weight: 600;
  }
  span {
    font-size: 14px;
    color: #777;
  }
`;