import React from "react";
import styled from "styled-components";

// --- PASO 1: Mantén tus importaciones de imágenes PNG ---
// Ya tienes esto, ¡perfecto!
import libro from "../../assets/img/iconos/libro.png";
import aprendiendo from "../../assets/img/iconos/aprendiendo.png";
import discusion from "../../assets/img/iconos/discusion.png";
import formacion from "../../assets/img/iconos/formacion.png";
import instructor from "../../assets/img/iconos/instructor.png";

// Ya no necesitas los iconos SVG, así que puedes borrarlos si no los usas en otro lado.
// import RollerIcon from "../../assets/svg/Services/RollerIcon";
// ...etc

export default function ServiceBox({icon, title, subtitle}) {
  // --- PASO 2: Modifica el switch para que devuelva la fuente (src) de la imagen ---
  let iconSrc; // Cambiamos el nombre de la variable para mayor claridad

  switch (icon) {
    case "libro":
      iconSrc = libro;
      break;
    case "aprendiendo":
      iconSrc = aprendiendo;
      break;
    case "discusion":
      iconSrc = discusion;
      break;
    case "formacion":
      iconSrc = formacion;
      break;
    case "instructor":
      iconSrc = instructor;
      break;
    default: // Un valor por defecto por si se pasa un icono no válido
      iconSrc = libro;
      break;
  }

  return (
    <Wrapper className="flex flexColumn">
      {/* --- PASO 3: Renderiza una etiqueta <img> con el src obtenido --- */}
      <IconStyle>
        <img src={iconSrc} alt={title} />
      </IconStyle>
      <TitleStyle className="font20 extraBold">{title}</TitleStyle>
      <SubtitleStyle className="font13">{subtitle}</SubtitleStyle>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  width: 100%;
  text-align: center; /* Centramos todo el contenido del box */
`;

const IconStyle = styled.div`
  margin: 0 auto; /* Siempre centrado */

  /* --- PASO 4: Añade estilos para la imagen dentro del div --- */
  img {
    width: 80px;  /* Define un ancho para tu icono */
    height: 80px; /* Define un alto para tu icono */
    object-fit: contain; /* Asegura que la imagen no se deforme */
  }
`;

const TitleStyle = styled.h2`
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
  padding: 30px 0 20px 0; /* Ajuste de padding */
`;

const SubtitleStyle = styled.p`
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
`;