import React from "react";
import styled from "styled-components";
// Components
import ProjectBox from "../Elements/ProjectBox";
import FullButton from "../Buttons/FullButton";
// Assets
import ProjectImg1 from "../../assets/img/escuelas/econo.png";
import ProjectImg2 from "../../assets/img/escuelas/minas.png";
import ProjectImg3 from "../../assets/img/escuelas/telec.png";
import ProjectImg4 from "../../assets/img/projects/4.png";
import ProjectImg5 from "../../assets/img/projects/5.png";
import ProjectImg6 from "../../assets/img/projects/6.png";
import AddImage2 from "../../assets/img/add/add2.png";


//pdfs

import MallaAgroPDF from "../../assets/pdfs/AGRO_MC.pdf";
import MallaArquiPDF from "../../assets/pdfs/ARQUI_MC.pdf";
import MallaContaPDF from "../../assets/pdfs/CONTA_MC.pdf";
import MallaEconoPDF from "../../assets/pdfs/ECONO_MC.pdf";
import MallaMinasPDF from "../../assets/pdfs/MINAS_MC.pdf";
import MallaTelePDF from "../../assets/pdfs/TELECO_MC.pdf";

export default function Escuelas() {
  return (
    <Wrapper id="escuelas">
      <div className="whiteBg">
        <div className="container">
          <HeaderInfo>
            <h1 className="font40 extraBold">Escuelas Profesionales</h1>
            <p className="font13">
              La sede Azángaro ofrece los siguientes programas académicos
            </p>
          </HeaderInfo>
          <div className="row textCenter">
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg1}
                title="Escuela Profesional de Ingeniería Económica"
                text="Forma especialistas en la aplicación de métodos de ingeniería y económicos para optimizar proyectos de inversión y gestión financiera."
                action={() => window.open("https://www.fieunap.edu.pe/", "_blank")}
                nombrebotton={"Plan de Estudios"}
                link={MallaEconoPDF}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg2}
                title="Escuela Profesional de Ingeniería de Minas"
                text="Forma profesionales para explorar y explotar los recursos minerales, aplicando tecnologías de extracción y procesamiento responsables."
                action={() => window.open("https://minas.unap.edu.pe/", "_blank")}
                link={MallaMinasPDF}
                nombrebotton={"Plan de Estudios"}
              />
            </div>
            <div className="col-xs-12 col-sm-4 col-md-4 col-lg-4">
              <ProjectBox
                img={ProjectImg3}
                title="Escuela Profesional de Ingeniería de Telecomunicaciones"
                text="Se enfoca en el diseño y operación de redes y sistemas de comunicación, incluyendo infraestructura satelital y de telecomunicaciones."
                action={() => window.open("https://portal.unap.edu.pe/", "_blank")}
                link={MallaTelePDF}
                nombrebotton={"Plan de Estudios"}
              />
            </div>
          </div>
          <br />
          <br />
        </div>
      </div>
     
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
    text-align: center;
  }
`;
const Advertising = styled.div`
  padding: 100px 0;
  margin: 100px 0;
  position: relative;
  @media (max-width: 1160px) {
    padding: 60px 0 40px 0;
  }
  @media (max-width: 860px) {
    flex-direction: column;
    padding: 0 0 30px 0;
    margin: 80px 0 0px 0;
  }
`;
const ButtonsRow = styled.div`
  @media (max-width: 860px) {
    justify-content: space-between;
  }
`;
const AddLeft = styled.div`
  position: relative;
  width: 50%;
  p {
    max-width: 475px;
  }
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
    text-align: center;
    h2 {
      line-height: 3rem;
      margin: 15px 0;
    }
    p {
      margin: 0 auto;
    }
  }
`;
const AddRight = styled.div`
  width: 50%;
  @media (max-width: 860px) {
    width: 80%;
    order: 2;
  }
`;
const AddLeftInner = styled.div`
  width: 100%;
  position: absolute;
  top: -300px;
  left: 0;
  @media (max-width: 1190px) {
    top: -250px;
  }
  @media (max-width: 920px) {
    top: -200px;
  }
  @media (max-width: 860px) {
    order: 1;
    position: relative;
    top: -60px;
    left: 0;
  }
`;
const ImgWrapper = styled.div`
  width: 100%;
  padding: 0 15%;
  img {
    width: 100%;
    height: auto;
  }
  @media (max-width: 400px) {
    padding: 0;
  }
`;
