import React from "react";
import Slider from "react-slick";
import styled from "styled-components";
import { FaChevronRight, FaChevronLeft } from "react-icons/fa"; // Usando react-icons para un look profesional

// Importa las hojas de estilo de react-slick
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

// --- PASO 1: Importa aquí todas tus fotos ---
// Reemplaza estos nombres con las rutas a tus imágenes.
import LugarFoto01 from "../../assets/img/SEDE/azangaro1.png";
import LugarFoto02 from "../../assets/img/SEDE/azangaro2.png";
import LugarFoto03 from "../../assets/img/SEDE/azangaro3.png";
import LugarFoto04 from "../../assets/img/SEDE/azangaro4.png";
import LugarFoto05 from "../../assets/img/SEDE/azangaro5.png";
import LugarFoto06 from "../../assets/img/SEDE/azangaro6.png";
import LugarFoto07 from "../../assets/img/SEDE/azangaro7.png";
import LugarFoto08 from "../../assets/img/SEDE/juli1.png"; 
import LugarFoto09 from "../../assets/img/SEDE/juli2.png"; 
import LugarFoto10 from "../../assets/img/SEDE/juli3.png"; 
import LugarFoto11 from "../../assets/img/SEDE/juli4.png";
import LugarFoto12 from "../../assets/img/SEDE/juli5.png";
   


const photos = [
  { id: 1, src: LugarFoto01, alt: "Descripción de la foto 1" },
  { id: 2, src: LugarFoto02, alt: "Descripción de la foto 2" },
  { id: 3, src: LugarFoto03, alt: "Descripción de la foto 3" },
  { id: 4, src: LugarFoto04, alt: "Descripción de la foto 4" },
  { id: 5, src: LugarFoto05, alt: "Descripción de la foto 5" },
  { id: 6, src: LugarFoto06, alt: "Descripción de la foto 6" },
  { id: 7, src: LugarFoto07, alt: "Descripción de la foto 7" },
  { id: 8, src: LugarFoto08, alt: "Descripción de la foto 8" },
  { id: 9, src: LugarFoto09, alt: "Descripción de la foto 9" },
  { id: 10, src: LugarFoto10, alt: "Descripción de la foto 10" },
  { id: 11, src: LugarFoto11, alt: "Descripción de la foto 11" },
  { id: 12, src: LugarFoto12, alt: "Descripción de la foto 12" },
];

// --- Componentes de Flecha Personalizados ---
const Arrow = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  transition: background-color 0.3s ease;
  color: white;
  font-size: 20px;

  &:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }

  &.next { right: 25px; }
  &.prev { left: 25px; }
`;

const CustomNextArrow = ({ onClick }) => (
  <Arrow className="next" onClick={onClick}>
    <FaChevronRight />
  </Arrow>
);

const CustomPrevArrow = ({ onClick }) => (
  <Arrow className="prev" onClick={onClick}>
    <FaChevronLeft />
  </Arrow>
);

export default function FullWidthMultiCarousel() {
  const settings = {
    dots: false, // Con múltiples items, los dots pueden ser redundantes
    infinite: true,
    speed: 500,
    slidesToShow: 4, // Muestra 4 imágenes en pantallas grandes
    slidesToScroll: 1, // Se desplaza de una en una
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: true,
    nextArrow: <CustomNextArrow />,
    prevArrow: <CustomPrevArrow />,
    // --- CLAVE: La configuración responsiva para ajustar el número de slides ---
    responsive: [
      {
        breakpoint: 1280, // Para pantallas más pequeñas que 1280px
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 768, // Para tablets
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 480, // Para móviles
        settings: {
          slidesToShow: 1,
        },
      },
    ],
  };

  return (
    <CarouselWrapper>
      <StyledSlider {...settings}>
        {photos.map((photo) => (
          <SlideWrapper key={photo.id}>
            <ImgStyle src={photo.src} alt={photo.alt} />
          </SlideWrapper>
        ))}
      </StyledSlider>
    </CarouselWrapper>
  );
}

// --- Estilos para el Carrusel de Ancho Completo ---

const CarouselWrapper = styled.div`
  width: 100%; /* El carrusel ocupa todo el ancho, sin márgenes */
  position: relative; /* Necesario para posicionar las flechas personalizadas */
  
  /* Prevenir selección para todo el carrusel */
  user-select: none;
  -webkit-user-select: none;
  
  /* Eliminar outline para todos los elementos dentro del carrusel */
  * {
    outline: none;
  }
  
  /* Eliminar highlight al tocar en dispositivos móviles */
  -webkit-tap-highlight-color: transparent;
`;

const SlideWrapper = styled.div`
  /* Este contenedor es necesario para aplicar el padding correctamente */
  padding: 0 10px; /* Crea un espacio horizontal entre las fotos */
  box-sizing: border-box;
  outline: none;
  
  &:focus {
    outline: none;
  }
`;

const ImgStyle = styled.img`
  width: 100%;
  height: 350px; /* Una altura fija para todas las imágenes */
  object-fit: cover; /* Esencial para que las imágenes no se deformen */
  border-radius: 8px; /* Opcional: para suavizar las esquinas */
  outline: none;
  -webkit-tap-highlight-color: transparent;
  
  /* Prevenir arrastre de imágenes */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
`;
const StyledSlider = styled(Slider)`
  &:focus {
    outline: none;
  }
  
  .slick-slide {
    outline: none;
    
    &:focus, &:active {
      outline: none;
    }
  }
`;