import React from 'react';

const MisionVision = () => (
    <section className="mision-vision-section" style={{ 
        padding: '5rem 1.5rem', 
        background: 'linear-gradient(to bottom, #f9f9f9, #ffffff)',
        boxShadow: 'inset 0 0 20px rgba(0,0,0,0.05)'
    }}>
        <div style={{ maxWidth: '1000px', margin: '0 auto' }}>
            <h2 style={{ 
                textAlign: 'center', 
                marginBottom: '3rem', 
                fontSize: '2.5rem',
                color: '#003366',
                fontWeight: '700',
                position: 'relative',
                paddingBottom: '15px'
            }}>
                Misión y Visión
                <span style={{ 
                    display: 'block', 
                    width: '80px', 
                    height: '4px', 
                    background: '#ff5722', 
                    margin: '15px auto 0' 
                }}></span>
            </h2>
            
            <div style={{ 
                display: 'flex', 
                flexDirection: 'row', 
                gap: '2.5rem',
                flexWrap: 'wrap',
                justifyContent: 'center'
            }}>
                <div style={{ 
                    flex: '1 1 400px',
                    background: '#ffffff',
                    borderRadius: '10px',
                    padding: '2rem',
                    boxShadow: '0 5px 15px rgba(0,0,0,0.08)',
                    transform: 'translateY(0)',
                    transition: 'transform 0.3s ease, box-shadow 0.3s ease',
                    border: '1px solid #e0e0e0'
                }} className="hover-card">
                    <div style={{ display: 'flex', alignItems: 'center', marginBottom: '1rem' }}>
                        <div style={{ 
                            background: '#003366', 
                            color: 'white', 
                            width: '40px', 
                            height: '40px', 
                            borderRadius: '50%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            marginRight: '15px',
                            fontSize: '1.2rem'
                        }}>M</div>
                        <h3 style={{ 
                            margin: 0, 
                            color: '#003366', 
                            fontSize: '1.5rem', 
                            fontWeight: '600' 
                        }}>Misión</h3>
                    </div>
                    <p style={{ 
                        lineHeight: '1.8', 
                        fontSize: '1.05rem',
                        color: '#444444'
                    }}>
                        La Universidad Nacional del Altiplano de Puno, a través de sus sedes descentralizadas, tiene como misión brindar una formación académica integral, científica, humanística y con responsabilidad social, orientada a la excelencia, la equidad y la inclusión educativa. Garantizando el acceso a una educación superior pública de calidad en las diversas provincias del departamento de Puno, promoviendo la investigación contextualizada, la innovación tecnológica y la extensión universitaria, con el fin de contribuir al desarrollo sostenible, la interculturalidad y el fortalecimiento de la identidad regional.
                    </p>
                </div>
                
                <div style={{ 
                    flex: '1 1 400px',
                    background: '#ffffff',
                    borderRadius: '10px',
                    padding: '2rem',
                    boxShadow: '0 5px 15px rgba(0,0,0,0.08)',
                    transform: 'translateY(0)',
                    transition: 'transform 0.3s ease, box-shadow 0.3s ease',
                    border: '1px solid #e0e0e0'
                }} className="hover-card">
                    <div style={{ display: 'flex', alignItems: 'center', marginBottom: '1rem' }}>
                        <div style={{ 
                            background: '#ff5722', 
                            color: 'white', 
                            width: '40px', 
                            height: '40px', 
                            borderRadius: '50%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            marginRight: '15px',
                            fontSize: '1.2rem'
                        }}>V</div>
                        <h3 style={{ 
                            margin: 0, 
                            color: '#003366', 
                            fontSize: '1.5rem', 
                            fontWeight: '600' 
                        }}>Visión</h3>
                    </div>
                    <p style={{ 
                        lineHeight: '1.8', 
                        fontSize: '1.05rem',
                        color: '#444444'
                    }}>
                        Ser una institución académica e investigación de referencia nacional en el ámbito de la educación superior descentralizada, reconocidas por su calidad educativa, pertinencia territorial, compromiso con la investigación, y por su activa contribución al desarrollo humano, científico, tecnológico, ambiental y sociocultural de la región de Puno y el Perú.
                    </p>
                </div>
            </div>
        </div>
        
        <style jsx>{`
            .hover-card:hover {
                transform: translateY(-5px);
                box-shadow: 0 10px 25px rgba(0,0,0,0.15);
            }
        `}</style>
    </section>
);

export default MisionVision;