import React from "react";
import styled from "styled-components";
import { Link } from "react-scroll";

// Assets
import LogoImg from "../../assets/svg/Logo"; // Asegúrate que la ruta a tu logo sea correcta
import LogoUnap from "../../assets/img/logo_unap_solo.png";
// Icons
import { FaMapMarkerAlt, FaPhoneAlt, FaEnvelope } from 'react-icons/fa';
import { FaFacebook, FaTwitter, FaYoutube } from 'react-icons/fa';

export default function Footer() {
  const getCurrentYear = () => {
    return new Date().getFullYear();
  };

  return (
    <Wrapper>
      <div className="container">
        {/* SECCIÓN PRINCIPAL DEL FOOTER CON COLUMNAS */}
        <InnerWrapper>
          {/* Columna 1: Logo e Identidad */}
          <Column>
            <Link className="pointer flexNullCenter" to="home" smooth={true}>
              <LogoBox>
                <img 
                  src={LogoUnap} 
                  alt="Logo UNAP" 
                  style={{ height: "50px" }}
                />
              </LogoBox>
            </Link>
            <p className="whiteColor font13" style={{ padding: "30px 0 0 0" }}>
              Universidad Nacional del Altiplano. Comprometidos con la excelencia académica y el desarrollo regional.
            </p>
          </Column>

          {/* Columna 2: Contacto */}
          <Column>
            <Title className="font15 extraBold whiteColor">Contacto</Title>
            <ContactItem>
              <IconCircle><FaMapMarkerAlt /></IconCircle>
              <p className="whiteColor font13" style={{ marginLeft: "10px" }}>
                Sede Central: Av. Floral Nº 1153, Puno - Perú
              </p>
            </ContactItem>
            <ContactItem>
              <IconCircle><FaPhoneAlt /></IconCircle>
              <p className="whiteColor font13" style={{ marginLeft: "10px" }}>
                Central Telefónica: (051) 363682
              </p>
            </ContactItem>
            {/* <ContactItem>
              <FaEnvelope color="#7620ff" />
              <p className="whiteColor font13" style={{ marginLeft: "10px" }}>
                OTI: tecnologias@unap.edu.pe
              </p>
            </ContactItem> */}
          </Column>

          {/* Columna 3: Enlaces de Interés */}
          <Column>
            <Title className="font15 extraBold whiteColor">Enlaces de Interés</Title>
            <FooterLink href="https://admision.unap.edu.pe/" target="_blank" rel="noopener noreferrer">Admisión</FooterLink>
            <FooterLink href="https://portal.unap.edu.pe/" target="_blank" rel="noopener noreferrer">Pagina Principal UNA-Puno</FooterLink>
            <FooterLink href="https://intranet.unap.edu.pe/" target="_blank" rel="noopener noreferrer">Intranet</FooterLink>
            <FooterLink href="https://transparencia.unap.edu.pe/" target="_blank" rel="noopener noreferrer" >Transparencia</FooterLink>
          </Column>

          {/* Columna 4: Redes Sociales */}
          <Column>
             <Title className="font15 extraBold whiteColor">Síguenos</Title>
             <SocialLinks>
              <SocialIcon href="https://www.facebook.com/unapunooficial" target="_blank" rel="noopener noreferrer"><FaFacebook /></SocialIcon>
              {/* <a href="https://twitter.com/unano" target="_blank" rel="noopener noreferrer"><SocialIcon><FaTwitter /></SocialIcon></a>
              <a href="https://www.youtube.com/unapuno" target="_blank" rel="noopener noreferrer"><SocialIcon><FaYoutube /></SocialIcon></a> */}
             </SocialLinks>
          </Column>

        </InnerWrapper>
        
        {/* BARRA INFERIOR DE COPYRIGHT */}
        <BottomBar>
          <StyleP className="whiteColor font13">
            © {getCurrentYear()} -{" "}
            <a
              href="https://oti.unap.edu.pe/"
              target="_blank"
              rel="noopener noreferrer"
              className="purpleColor font13"
              style={{ textDecoration: "underline", color: "#7620FF" }}
            >
              Universidad Nacional del Altiplano - Oficina de Tecnologías de la Información
            </a>
            . Todos los derechos reservados.
          </StyleP>
          <Link className="whiteColor animate pointer font13" to="home" smooth={true} offset={-80}>
            Volver Arriba
          </Link>
        </BottomBar>
      </div>
    </Wrapper>
  );
}

// --- STYLED COMPONENTS ---

const Wrapper = styled.div`
  width: 100%;
  background: #212121; /* Fondo oscuro global para el footer */
  padding: 40px 0 0 0;
`;

const InnerWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 60px 0 40px 0;
  gap: 30px;

  @media (max-width: 768px) {
    flex-direction: column;
    gap: 0;
  }
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  min-width: 180px;
  padding: 0 12px;
`;

const LogoBox = styled.div`
  /* background: #fff; */ /* Quitado el fondo blanco */
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(76, 0, 255, 0.10);
  padding: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
`;

const Title = styled.h3`
  margin-bottom: 20px;
  color: #fff;
  font-weight: 700;
  letter-spacing: 0.5px;
`;

const ContactItem = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 14px;
`;

const IconCircle = styled.div`
  background: #7620ff;
  color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  aspect-ratio: 1 / 1; /* Fuerza círculo perfecto */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  box-shadow: 0 2px 8px rgba(76, 0, 255, 0.10);
`;

const FooterLink = styled.a`
  color: #fff;
  font-size: 14px;
  margin-bottom: 12px;
  text-decoration: none;
  border-radius: 4px;
  padding: 4px 8px;
  transition: background 0.3s, color 0.3s;
  background: transparent;

  &:hover {
    background: #7620ff22;
    color: #7620ff;
  }
`;

const SocialLinks = styled.div`
  display: flex;
  gap: 18px;
`;

const SocialIcon = styled.a`
  background: #fff;
  color: #7620ff;
  border-radius: 50%;
  font-size: 22px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(76, 0, 255, 0.10);
  transition: background 0.3s, color 0.3s;

  &:hover {
    background: #7620ff;
    color: #fff;
  }
`;

const BottomBar = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-top: 1px solid #444;
  margin-top: 20px;

  @media (max-width: 550px) {
    flex-direction: column;
  }
`;

const StyleP = styled.p`
  color: #fff;
  font-size: 13px;

  @media (max-width: 550px) {
    margin-bottom: 20px;
  }
`;