import React from "react";
import styled from "styled-components";
// Components
import FullButton from "../Buttons/FullButton";
// Assets
import HeaderImage from "../../assets/img/SEDE/azangaro1.png";

import QuotesIcon from "../../assets/svg/Quotes";
import Dots from "../../assets/svg/Dots";

export default function HeaderIni() {
  return (
      <Wrapper id="home" className="container flexSpaceCenter">
      <LeftSide className="flexCenter">
        <div>
          <h1 className="extraBold font60">Universidad Nacional del Altiplano - Puno </h1>
          <h2 className="extraBold font30">Sedes Universitarias (Chucuito - Juli, Azángaro) </h2>
          {/* <HeaderP className="font13 semiBold">
            
          </HeaderP> */}
          {/* <BtnWrapper>
            <FullButton title="Get Started" />
          </BtnWrapper> */}
        </div>
      </LeftSide>
      <RightSide>
        <ImageWrapper>
          <ImageWrapper2>
            <Img2 src={HeaderImage} alt="office" />
          </ImageWrapper2>
          <QuoteWrapper className="flexCenter darkBg radius8">
            <QuotesWrapper>
              <QuotesIcon />
            </QuotesWrapper>
            <div>
              <p className="font15 whiteColor">
                <em>Se ha creado la sede en Azángaro, donde funcionan secciones de las carreras de Ingeniería de Minas, Ingeniería Económica e Ingeniería de Telecomunicaciones. También se ha creado la sede en Chucuito-Juli, donde funcionan las secciones de las carreras profesionales de Arquitectura y Urbanismo, Ingeniería Agroindustrial y Ciencias Contables.</em>
              </p>
              <p className="font13 orangeColor textRight" style={{marginTop: '10px'}}>Dr. Paulino Machaca Ari</p>
            </div>
          </QuoteWrapper>
          <DotsWrapper>
            <Dots />
          </DotsWrapper>
        </ImageWrapper>
        <GreyDiv className="lightBg"></GreyDiv>
      </RightSide>
    </Wrapper>
  );
}
const Img2 = styled.img`
  /* --- INICIO DE CAMBIOS --- */
  width: 100%;
  height: 100%;
  object-fit: cover; /* ¡LA OTRA PROPIEDAD CLAVE! Ajusta la imagen sin deformarla */
  /* --- FIN DE CAMBIOS --- */

  /* El código responsive de abajo ya no es necesario, lo podemos eliminar */
  /* @media (max-width: 560px) {
    width: 80%;
    height: auto;
  } */
`;
const ImageWrapper2 = styled.div`
  display: flex;
  justify-content: flex-end;
  position: relative;
  z-index: 9;
  
  /* --- INICIO DE CAMBIOS --- */
  width: 100%; /* Hacemos que ocupe el espacio disponible */
  max-width: 400px; /* Un ancho máximo para que no se haga gigante en pantallas grandes */
  aspect-ratio: 3 / 4; /* ¡LA PROPIEDAD CLAVE! Define la proporción */
  overflow: hidden; /* Oculta las partes de la imagen que se desbordan */
  border-radius: 8px; /* Movemos el radius aquí para que la máscara sea redondeada */
  /* --- FIN DE CAMBIOS --- */

  @media (max-width: 960px) {
    width: 100%;
    justify-content: center;
    /* Opcional: ajustar el tamaño en móvil si es necesario */
    max-width: 350px; 
    margin: 0 auto; /* Para centrarlo */
  }
`;
const Wrapper = styled.section`
  padding-top: 80px;
  width: 100%;
  min-height: 840px;
  @media (max-width: 960px) {
    flex-direction: column;
  }
`;
const LeftSide = styled.div`
  width: 50%;
  height: 100%;
  @media (max-width: 960px) {
    width: 100%;
    order: 2;
    margin: 50px 0;
    text-align: center;
  }
  @media (max-width: 560px) {
    margin: 80px 0 50px 0;
  }
`;
const RightSide = styled.div`
  width: 50%;
  height: 100%;
  @media (max-width: 960px) {
    width: 100%;
    order: 1;
    margin-top: 30px;
  }
`;
const HeaderP = styled.div`
  max-width: 470px;
  padding: 15px 0 50px 0;
  line-height: 1.5rem;
  @media (max-width: 960px) {
    padding: 15px 0 50px 0;
    text-align: center;
    max-width: 100%;
  }
`;
const BtnWrapper = styled.div`
  max-width: 190px;
  @media (max-width: 960px) {
    margin: 0 auto;
  }
`;
const GreyDiv = styled.div`
  width: 30%;
  height: 700px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  @media (max-width: 960px) {
    display: none;
  }
`;
const ImageWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  position: relative;
  z-index: 9;
  @media (max-width: 960px) {
    width: 100%;
    justify-content: center;
  }
`;
const Img = styled.img`
  @media (max-width: 560px) {
    width: 80%;
    height: auto;
  }
`;
const QuoteWrapper = styled.div`
  position: absolute;
  left: 0;
  bottom: 50px;
  max-width: 330px;
  padding: 30px;
  z-index: 99;
  @media (max-width: 960px) {
    left: 20px;
  }
  @media (max-width: 560px) {
    bottom: -50px;
  }
`;
const QuotesWrapper = styled.div`
  position: absolute;
  left: -20px;
  top: -10px;
`;
const DotsWrapper = styled.div`
  position: absolute;
  right: -100px;
  bottom: 100px;
  z-index: 2;
  @media (max-width: 960px) {
    right: 100px;
  }
  @media (max-width: 560px) {
    display: none;
  }
`;


